.news-page {
  background-color: @white;
  padding-top: 287px;
  padding-bottom: 188px;
}

.news-page__img-wrapper {
  height: 502px;
  width: 40%;
  float: right;
}

.news-page__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.news-page__info {
  width: 100%;
  max-width: 768px;
}

.news-page__title {
  font-size: 62px;
  line-height: 76px;
  margin-bottom: 70px;
  color: #000;
}

.news-page__txt {
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
  color: #000;
}
