.connection {
  background-image: url("../img/form-bg.png");
  background-size: cover;
  background-position: 50%;
  background-repeat: no-repeat;

  form {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-wrap: wrap;
    height: 160px;
  }

  input {
    padding: 16px 28px;
    width: 533px;
    border-radius: 6px;
    border: 1px solid #707070;
    font-size: 18px;
    line-height: 36px;

    &:first-child {
      margin-bottom: 20px;
    }

    &::placeholder {
      font-size: 18px;
      line-height: 36px;
      color: #000000;
      font-weight: 500;
    }
  }

  textarea {
    width: 533px;
    height: 160px;
    border-radius: 6px;
    border: 1px solid #707070;
    padding: 16px 30px;

    &::placeholder {
      font-size: 18px;
      line-height: 36px;
      color: #000000;
      font-weight: 500;
    }
  }

  input[type=submit] {
    color: @white;
    font-family: @mont;
    font-size: 18px;
    line-height: 29px;
    font-weight: 600;
    width: 316px;
    padding: 20px 21px;
    border-radius: 6px;
    background-color: @gold;
    border: none;
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
  }
}

.connection__title {
  font-size: 48px;
  line-height: 58px;
  color: @white;
  margin-bottom: 52px;
}

.connection__wrapper {
  padding-top: 120px;
  padding-bottom: 150px;
}

