.partners {
  background-color: @white;
}

.partners__wrapper {
  padding-top: 120px;
  padding-bottom: 142px;
}

.service__title {
  font-size: 48px;
  line-height: 58px;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 70px;
}

.partners__list {
  display: flex;
  list-style: none;
}

.partners__list-item-wrapper {
  width: 100%;
  max-width: 374px;
  height: 534px;
  position: relative;
  margin-bottom: 27px;
}

.partners__img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.parnters__dscr {
  display: none;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: @black;
  color: @white;
  padding: 21px;
  font-size: 18px;
  line-height: 21px;
  font-weight: 500;
}

.partners__switch {
  width: 49px;
  height: 49px;
  border-radius: 50%;
  background-image: url("../img/book-icon.svg");
  background-size: 23px 23px;
  background-position: 50%;
  background-repeat: no-repeat;
  background-color: @white;
  border: none;
  position: absolute;
  bottom: 20px;
  left: 20px;
}

.partners__switch--close {
  background-image: none;

  &::after {
    content: "";
    display: block;
    width: 30px;
    height: 2px;
    transform: rotate(-45deg);
    position: absolute;
    top: 23px;
    left: 10px;
    background-color: @black;
    border-radius: 2px;
  }

  &::before {
    content: "";
    display: block;
    width: 30px;
    height: 2px;
    transform: rotate(45deg);
    position: absolute;
    top: 23px;
    left: 10px;
    background-color: @black;
    border-radius: 2px;
  }
}

.partners__mailto {
  width: 49px;
  height: 49px;
  border-radius: 50%;
  background-image: url("../img/002-email_3.svg");
  background-size: cover;
  background-position: 50%;
  background-repeat: no-repeat;
  background-color: @white;
  border: none;
  position: absolute;
  bottom: 20px;
  left: 79px;
}

.partners__name {
  font-size: 32px;
  line-height: 36px;
  font-weight: 600;
  margin-bottom: 20px;
}

.partners__info-dscr {
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
  padding-right: 40px;
}
