.footer {
  background-color: #000;
}

.footer__wrapper {
  padding-top: 138px;
  padding-bottom: 68px;
  display: flex;
  justify-content: space-between;
}

.footer__logo {
  font-size: 29px;
  line-height: 1;
  color: @white;
  margin-bottom: 120px;
  display: block;
}

.footer__contacts-list {
  margin-bottom: 174px;
}

.footer__contacts-list-item {
  margin-bottom: 30px;

  &:last-child {
    padding-left: 45px;
    margin-right: 30px;
    position: relative;
    font-size: 16px;
    line-height: 19px;
    font-weight: 500;
    color: @white;

    &::after {
      content: "";
      display: block;
      width: 15px;
      height: 24px;
      background-image: url("../img/003-location.svg");
      background-position: 0 0;
      background-size: 15px 24px;
      background-repeat: no-repeat;
      position: absolute;
      top: 0;
      left: 0;
    }
  }
}

.footer__tel {
  padding-left: 45px;
  margin-right: 30px;
  position: relative;
  font-size: 16px;
  line-height: 19px;
  font-weight: 500;
  color: @white;

  &::after {
    content: "";
    display: block;
    width: 24px;
    height: 24px;
    background-image: url("../img/001-phone_2.svg");
    background-position: 0 0;
    background-size: 24px 24px;
    background-repeat: no-repeat;
    position: absolute;
    top: 0;
    left: 0;
  }
}

.footer__mail {
  padding-left: 45px;
  margin-right: 50px;
  position: relative;
  font-size: 16px;
  line-height: 19px;
  font-weight: 500;
  color: @white;

  &::after {
    content: "";
    display: block;
    width: 24px;
    height: 18px;
    background-image: url("../img/002-email_2.svg");
    background-position: 0 0;
    background-size: 24px 18px;
    background-repeat: no-repeat;
    position: absolute;
    top: 0;
    left: 0;
  }
}

.footer__rights {
  font-size: 12px;
  line-height: 15px;
  color: @white;
}

.footer__nav-list {

}

.footer__nav-list-item {
  text-align: right;
  margin-bottom: 18px;

  a {
    font-size: 24px;
    line-height: 36px;
    font-weight: 600;
    color: #e6e6e6;
    opacity: 0.5;
  }
}

.footer__nav-list-item--active {

  a {
    color: @white;
    opacity: 1;
  }
}
