.pro-bono {
  background-color: @gray;
  background-image: url("../img/pro-bono-back.jpg");
  background-size: cover;
  background-position: 50%;
  background-repeat: no-repeat;
}

.pro-bono__wrapper {
  padding-top: 120px;
  padding-bottom: 175px;
  display: flex;
  justify-content: space-between;
}

.pro-bono__title {
  font-size: 48px;
  line-height: 58px;
  text-transform: uppercase;
}

.pro-bono__info {
  width: 100%;
  max-width: 690px;
}

.pro-bono__txt {
  font-size: 24px;
  line-height: 36px;
  color: #100f0f;
}

.pro-bono__wrapper-inner {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: flex-start;
  margin-top: 133px;
}

.pro-bono__btn-dscr {
  width: 100%;
  max-width: 375px;
  font-size: 18px;
  line-height: 24px;
  color: @black;
}
