.info {

}

.info__wrapper {
  padding-top: 100px;
  padding-bottom: 80px;
  display: flex;
}

.info__title {
  width: 100%;
  max-width: 362px;
  margin-right: 112px;

  span {
    font-size: 48px;
    line-height: 58px;
    display: block;
    width: 100%;
    text-transform: uppercase;

    &:nth-child(2) {
      text-align: right;
    }
  }
}

.info__txt-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.info__txt {
  width: 100%;
  max-width: 520px;
  margin-right: 20px;
}
