.logo {
  font-weight: 900;
  transition: all ease 0.5s;
}

.logo__decor {
  display: inline-block;
  width: 8px;
  height: 8px;
  background-color: @gold;
  transition: all ease 0.5s;
}

.logo--service {
  position: relative;

  .logo__decor {
    position: absolute;
    height: 13px;
    width: 40vw;
    bottom: 0;
    transform: translateX(calc(10px));

  }
}
