.arrow {
  width: 30px;
  height: 2px;
  background-color: @gold;
  position: relative;
  display: block;

  &::after {
    content: "";
    display: block;
    width: 10px;
    height: 2px;
    transform: rotate(45deg);
    position: absolute;
    top: -4px;
    right: 0;
    background-color: @gold;
  }

  &::before {
    content: "";
    display: block;
    width: 10px;
    height: 2px;
    transform: rotate(-45deg);
    position: absolute;
    bottom: -4px;
    right: 0;
    background-color: @gold;
  }
}
