.main-popup__close {
  background-image: none;
  width: 42px;
  height: 42px;
  border-radius: 50%;
  border: none;
  position: absolute;
  top: -20px;
  right: -20px;
  background-color: @white;
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);

  &::after {
    content: "";
    display: block;
    width: 25px;
    height: 2px;
    transform: rotate(-45deg);
    position: absolute;
    top: 20px;
    left: 9px;
    background-color: #707070;
    border-radius: 2px;
  }

  &::before {
    content: "";
    display: block;
    width: 25px;
    height: 2px;
    transform: rotate(45deg);
    position: absolute;
    top: 20px;
    left: 9px;
    background-color:  #707070;
    border-radius: 2px;
  }
}

.main-popup__title {
  font-size: 24px;
  line-height: 36px;
  font-weight: 600;
  text-align: center;
  display: block;
  margin-bottom: 46px;
}

.main-popup {
  display: none;
  width: 100%;
  max-width: 384px;
  padding-top: 45px;
  padding-left: 48px;
  padding-right: 48px;
  padding-bottom: 24px;
  background-color: @white;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 7;

  input {
    padding: 14px 28px;
    width: 100%;
    border-radius: 6px;
    font-size: 16px;
    line-height: 19px;
    margin-bottom: 24px;
    background-color: #f4f4f4;
    border: none;

    &::placeholder {
      font-size: 16px;
      line-height: 19px;
      color: #707070;
      font-weight: 500;
    }
  }

  textarea {
    width: 100%;
    height: 160px;
    border-radius: 6px;
    padding: 14px 30px;
    font-size: 16px;
    line-height: 19px;
    margin-bottom: 24px;
    background-color: #f4f4f4;
    border: none;

    &::placeholder {
      font-size: 16px;
      line-height: 19px;
      color: #707070;
      font-weight: 500;
    }
  }

  input[type=submit] {
    color: @white;
    font-family: @mont;
    font-size: 16px;
    line-height: 19px;
    font-weight: 600;
    width: 100%;
    padding: 20px 21px;
    border-radius: 6px;
    background-color: #000;
    border: none;
  }
}
