.cases {
  background-color: @white;
}

.cases__wrapper {
  padding-top: 120px;
  padding-bottom: 150px;
}

.cases__title {
  font-size: 48px;
  line-height: 58px;
  margin-bottom: 90px;
}

.cases__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: flex-start;
}

.cases__list-item {
  width: 100%;
  max-width: 770px;
  background-color: @gray;
  padding: 36px 59px;
  padding-bottom: 14px;
  margin-bottom: 20px;
}

.cases__list-item-title {
  display: flex;
  align-items: center;
  min-height: 58px;
  padding-left: 78px;
  position: relative;
  margin-bottom: 22px;
}

.cases__icon-wrapper {
  width: 58px;
  height: 58px;
  background-color: @white;
  border-radius: 50%;
  position: absolute;
  padding: 3px;
  top: 0;
  left: 0;
}

.cases__icon {
  object-fit: cover;
  margin-top: 50%;
  margin-left: 50%;
  transform: translate(-50%, -50%);
}

.cases__list-item-title-bold {
  font-size: 32px;
  line-height: 36px;
  color: #000;
  font-weight: 500;
}

.cases__btn-arrow {
  display: block;
  width: 37px;
  height: 37px;
  border: none;
  background-color: @white;
  position: relative;
  border-radius: 50%;
  margin-left: auto;
  transition: all ease-in 0.3s;
  transform: rotate(-90deg);

  &::after {
    content: "";
    display: block;
    width: 10px;
    height: 2px;
    transform: rotate(35deg);
    position: absolute;
    top: 18px;
    left: 10px;
    background-color: @gold;
    border-radius: 2px;
  }

  &::before {
    content: "";
    display: block;
    width: 10px;
    height: 2px;
    transform: rotate(-35deg);
    position: absolute;
    top: 18px;
    right: 10px;
    background-color: @gold;
    border-radius: 2px;
  }
}

.cases__dscr {
  font-size: 18px;
  line-height: 24px;
  color: #000;
  display: none;
  padding-bottom: 22px;
}

.cases__txt-bold {
  font-weight: 600;
}
