.services {
  background-color: @gray;
  overflow-x: hidden;
}

.services__wrapper {
  padding-top: 120px;
  padding-bottom: 120px;
}

.service__title {
  font-size: 48px;
  line-height: 58px;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 48px;
}

.services__list {
  display: flex;
  flex-wrap: wrap;
  list-style-type: none;
  /* Убираем исходные маркеры */
  counter-reset: num;
  /* Задаём имя счетчика */
}

.services__list-item {
  display: flex;
  flex-direction: column;
  font-size: 32px;
  line-height: 38px;
  font-weight: 600;
  margin-bottom: 100px;
  margin-right: 95px;
  position: relative;

  &::before {
    content: counter(num, decimal-leading-zero);
    /* Выводим число */
    counter-increment: num;
    /* Увеличиваем значение счётчика */
    font-size: 100px;
    line-height: 122px;
    color: @gold;
    font-weight: 500;
    position: relative;
    z-index: 1;
  }

  &::after {
    content: "";
    display: block;
    width: 80px;
    height: 80px;
    background-color: @white;
    position: absolute;
    top: 0;
    left: 40px;
    z-index: 0;
  }
}

.services__list-link {
  margin-top: 40px;
  display: block;
  height: 30px;
  position: relative;

  .services__btn-arrow {
    position: absolute;
    left: 0;
    top: 50%;
    width: 70px;
    background-color: @black;
    transform: translateY(-50%);

    &::before,
    &::after {
      background-color: @black;
    }
  }
}

.services__list-item--logo {
  align-self: flex-end;

  &::before {
    display: none;
  }

  &::after {
    display: none;
  }

  .services__logo {
    font-size: 52px;
    margin-bottom: 5px;
  }
}

.services__popup {
  width: 100%;
  max-width: 700px;
  padding: 36px 42px;
  border-radius: 6px;
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
  background-color: #ffffff;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 3;
  display: none;
}

.services__popup-title {
  display: block;
  font-size: 32px;
  line-height: 36px;
  margin-bottom: 42px;
}

.services__popup-list {
  margin-bottom: 145px;
}

.services__popup-list-item {
  font-size: 18px;
  line-height: 24px;
  margin-bottom: 8px;
  padding-left: 25px;
  position: relative;

  &::after {
    content: "";
    display: block;
    width: 15px;
    height: 2px;
    background-color: @gold;
    position: absolute;
    top: 50%;
    left: 5px;
    transform: translate(-50%, -50%);
  }
}

.services__close {
  background-image: none;
  width: 42px;
  height: 42px;
  border-radius: 50%;
  border: none;
  position: absolute;
  top: -20px;
  right: -20px;
  background-color: @white;
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);

  &::after {
    content: "";
    display: block;
    width: 25px;
    height: 2px;
    transform: rotate(-45deg);
    position: absolute;
    top: 20px;
    left: 9px;
    background-color: #707070;
    border-radius: 2px;
  }

  &::before {
    content: "";
    display: block;
    width: 25px;
    height: 2px;
    transform: rotate(45deg);
    position: absolute;
    top: 20px;
    left: 9px;
    background-color:  #707070;
    border-radius: 2px;
  }
}
