* {
  box-sizing: border-box;
}
body {
  padding: 0;
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 25px;
  font-family: "Montserrat", sans-serif;
  color: #121214;
}
ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
h1,
h2,
h3,
h4,
h5 {
  margin: 0;
}
.container {
  width: 100%;
  max-width: 1600px;
  padding: 0 20px;
  margin: 0 auto;
}
.vis-hidd {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}
a {
  text-decoration: none;
  transition: all ease-in 0.3s;
  outline: none;
}
a:active {
  opacity: 0.8;
}
button {
  outline: none;
}
p {
  margin: 0;
  margin-bottom: 20px;
}
.overlay {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 5;
  background-color: #121214;
}
.popup {
  z-index: 6!important;
}
.btn {
  color: #ffffff;
  font-family: "Montserrat", sans-serif;
  font-size: 18px;
  line-height: 29px;
  font-weight: 600;
  display: inline-block;
  padding: 20px 21px;
  border-radius: 6px;
}
.btn--black {
  background-color: #121214;
}
.btn--gold {
  background-color: #c1ac69;
}
.arrow {
  width: 30px;
  height: 2px;
  background-color: #c1ac69;
  position: relative;
  display: block;
}
.arrow::after {
  content: "";
  display: block;
  width: 10px;
  height: 2px;
  transform: rotate(45deg);
  position: absolute;
  top: -4px;
  right: 0;
  background-color: #c1ac69;
}
.arrow::before {
  content: "";
  display: block;
  width: 10px;
  height: 2px;
  transform: rotate(-45deg);
  position: absolute;
  bottom: -4px;
  right: 0;
  background-color: #c1ac69;
}
.header__burger {
  height: 25px;
  width: 30px;
  position: relative;
  background-color: transparent;
  border: none;
  outline: none;
}
.header__burger::after {
  content: "";
  display: block;
  width: 100%;
  height: 3px;
  background-color: #000;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 1px;
}
.header__burger::before {
  content: "";
  display: block;
  width: 100%;
  height: 3px;
  background-color: #000;
  position: absolute;
  bottom: 0;
  left: 0;
  border-radius: 1px;
}
.header__burger .header__burger-decor {
  display: block;
  width: 100%;
  height: 3px;
  background-color: #000;
  position: absolute;
  top: 11px;
  right: 0;
  border-radius: 1px;
}
.header {
  background-image: url("../img/header-bg.png");
