.news {

}

.news__wrapepr {
  padding-top: 120px;
  padding-bottom: 150px;
}

.news__title {
  font-size: 48px;
  line-height: 58px;
  margin-bottom: 50px;
}

.news__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.news__list-item {
  width: 100%;
  max-width: 455px;
}

.news__list-title {
  font-size: 32px;
  line-height: 36px;
  margin-bottom: 33px;
}

.news__list-dscr {
  font-size: 18px;
  line-height: 24px;
}

.news__list-link {
  margin-top: 40px;
  display: block;
  height: 30px;
  position: relative;

  .news__btn-arrow {
    position: absolute;
    left: 0;
    top: 50%;
    width: 70px;
    background-color: @black;
    transform: translateY(-50%);

    &::before,
    &::after {
      background-color: @black;
    }
  }
}
