.header__burger {
  height: 25px;
  width: 30px;
  position: relative;
  background-color: transparent;
  border: none;
  outline: none;

  &::after {
    content: "";
    display: block;
    width: 100%;
    height: 3px;
    background-color: #000;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 1px;
  }

  &::before {
    content: "";
    display: block;
    width: 100%;
    height: 3px;
    background-color: #000;
    position: absolute;
    bottom: 0;
    left: 0;
    border-radius: 1px;
  }

  .header__burger-decor {
    display: block;
    width: 100%;
    height: 3px;
    background-color: #000;
    position: absolute;
    top: 11px;
    right: 0;
    border-radius: 1px;
  }
}

.header {
  background-image: url("../img/header-bg.png");
  background-size: 100% 100%;
  background-position: 50%;
  background-repeat: no-repeat;
}

.header--contacts {
  background-image: none;

  .header__wrapper {
    padding-top: 0;
    padding-bottom: 0;
  }

  .header__contacts-tel {
    color: @white;
  }

  .header__contacts-mail {
    color: @white;
  }

  .header__logo {
    color: @white;
  }

  .header__burger {
    &::before {
      background-color: @white;
    }

    &::after {
      background-color: @white;
    }

    .header__burger-decor {
      background-color: @white;
    }
  }

  .header__logo--menu {
    color: @black;
  }
}

.header--news {
  background-image: none;

  .header__wrapper {
    padding-top: 0;
    padding-bottom: 0;
  }

}

.header__wrapper {
  padding-top: 356px;
  padding-bottom: 200px;
}

.header__menu {
  display: block;
  position: fixed;
  width: 100vw;
  height: 100vh;
  background-color: @white;
  top: 0;
  right: 0;
  z-index: 5;

  .header__menu-close {
    background-image: none;
    width: 42px;
    height: 42px;
    border-radius: 50%;
    border: none;
    position: absolute;
    top: 5%;
    left: 0;
    background-color: @white;

    &::after {
      content: "";
      display: block;
      width: 25px;
      height: 2px;
      transform: rotate(-45deg);
      position: absolute;
      top: 20px;
      left: 9px;
      background-color: #707070;
      border-radius: 2px;
    }

    &::before {
      content: "";
      display: block;
      width: 25px;
      height: 2px;
      transform: rotate(45deg);
      position: absolute;
      top: 20px;
      left: 9px;
      background-color: #707070;
      border-radius: 2px;
    }
  }

  .header__menu-wrapper {
    display: flex;
    padding-top: 20vh;
    padding-right: 20vw;
    align-items: flex-end;
    justify-content: space-between;
    position: relative;
  }

  .header__nav-list-item {
    margin-bottom: 18px;

    a {
      font-size: 42px;
      line-height: 52px;
      font-weight: 600;
      color: #707070;
      opacity: 0.67;
    }
  }

  .header__nav-list-item--active {
    a {
      color: @black;
      opacity: 1;
    }
  }


  .header__contacts-list-item {
    margin-bottom: 30px;

    &:last-child {
      padding-left: 45px;
      margin-right: 30px;
      position: relative;
      font-size: 16px;
      line-height: 19px;
      font-weight: 500;
      color: @black;

      &::after {
        content: "";
        display: block;
        width: 15px;
        height: 24px;
        background-image: url("../img/003-location.svg");
        background-position: 0 0;
        background-size: 15px 24px;
        background-repeat: no-repeat;
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }

  .header__tel {
    padding-left: 45px;
    margin-right: 30px;
    position: relative;
    font-size: 16px;
    line-height: 19px;
    font-weight: 500;
    color: @black;

    &::after {
      content: "";
      display: block;
      width: 24px;
      height: 24px;
      background-image: url("../img/001-phone_2.svg");
      background-position: 0 0;
      background-size: 24px 24px;
      background-repeat: no-repeat;
      position: absolute;
      top: 0;
      left: 0;
    }
  }

  .header__mail {
    padding-left: 45px;
    margin-right: 50px;
    position: relative;
    font-size: 16px;
    line-height: 19px;
    font-weight: 500;
    color: @black;

    &::after {
      content: "";
      display: block;
      width: 24px;
      height: 18px;
      background-image: url("../img/002-email_2.svg");
      background-position: 0 0;
      background-size: 24px 18px;
      background-repeat: no-repeat;
      position: absolute;
      top: 0;
      left: 0;
    }
  }
}

.header__menu--fade {
  display: none;
}

.header__logo--menu {
  position: fixed;
  top: 20vh !important;
  left: 53vw !important;
  z-index: 10;
  font-size: 52px !important;
  line-height: 1;

  &::after {
    content: "уверенное развитие бизнеса";
    position: absolute;
    transform: translateY(10%);
    width: 100px;
    display: block;
    font-size: 32px;
    line-height: 36px;
    font-weight: 600;
  }

  .logo__decor {
    position: absolute;
    height: 13px;
    width: 40vw;
    bottom: 5px;
    transform: translateX(calc(10px));
  }
}

.header__top-wrapper {
  display: flex;
  margin-bottom: 250px;
  position: fixed;
  top: 72px;
  width: 100%;
  max-width: 1560px;
  margin-left: auto;
  z-index: 5;
}

.header__logo {
  font-size: 29px;
  line-height: 1;
  font-weight: 900;
  left: 10vw;
  top: 72px;
  transition: all ease 2s;
  z-index: 10;
}

.header__logo--static {
  font-size: 29px;
  line-height: 1;
  font-weight: 900;
  left: 10vw;
  top: 72px;
  // transition: all ease 0.5s;
}

.header__contacts {
  margin-left: auto;
}

.header__contacts-tel {
  padding-left: 45px;
  margin-right: 30px;
  position: relative;
  font-size: 16px;
  line-height: 19px;
  font-weight: 500;
  color: @black;

  &::after {
    content: "";
    display: block;
    width: 30px;
    height: 30px;
    background-image: url("../img/001-phone.svg");
    background-position: 0 0;
    background-size: 30px 30px;
    background-repeat: no-repeat;
    position: absolute;
    top: -6px;
    left: 0;
  }
}

.header__contacts-mail {
  padding-left: 45px;
  margin-right: 50px;
  position: relative;
  font-size: 16px;
  line-height: 19px;
  font-weight: 500;
  color: @black;

  &::after {
    content: "";
    display: block;
    width: 30px;
    height: 30px;
    background-image: url("../img/002-email.svg");
    background-position: 0 0;
    background-size: 30px 30px;
    background-repeat: no-repeat;
    position: absolute;
    top: -6px;
    left: 0;
  }
}

.header__title {
  font-size: 72px;
  line-height: 80px;
  font-weight: 700;
  width: 100%;
  max-width: 970px;
  margin-bottom: 240px;
  text-transform: uppercase;

  span {
    color: @gold;
  }
}

.header__wrapper-inner {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.header__btn {
  margin-top: 55px;
  position: relative;
  padding-right: 70px;
}

.header__btn-arrow {
  position: absolute;
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
}

.header__txt {
  width: 100%;
  max-width: 455px;
  font-size: 24px;
  line-height: 29px;
  font-weight: 500;
  padding-top: 55px;
  position: relative;

  &::after {
    content: "";
    display: block;
    width: 30px;
    height: 30px;
    background-color: @gold;
    background-position: 0 0;
    background-size: 30px 30px;
    background-repeat: no-repeat;
    position: absolute;
    top: 0;
    left: 0;
  }
}

.animate-bg {
  animation: bg 3.5s;
}

@keyframes bg {
  0% {
    background-size: 120% 120%;
  }

  100% {
    background-size: 100% 100%;
  }
}
