.career {
  background-color: @black;
  background-image: url("../img/carier-back.jpg");
  background-size: cover;
  background-position: 50%;
  background-repeat: no-repeat;
}

.career__wrapper {
  padding-top: 120px;
  padding-bottom: 145px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.career__title {
  color: @white;
  font-size: 48px;
  line-height: 58px;
  text-transform: uppercase;
}

.career__info {
  color: @white;
  max-width: 533px;
  font-size: 18px;
  line-height: 24px;
}
