.contacts {
  min-height: 1080px;
  background-color: @black;
  display: flex;
  align-items: flex-end;
  padding-bottom: 75px;
}

.contacts__list {
  margin-top: auto;
  margin-bottom: 42px;
}

.contacts__list-item {
  margin-bottom: 30px;

  &:last-child {
    padding-left: 45px;
    margin-right: 30px;
    position: relative;
    font-size: 16px;
    line-height: 19px;
    font-weight: 500;
    color: @white;

    &::after {
      content: "";
      display: block;
      width: 15px;
      height: 24px;
      background-image: url("../img/003-location-white.svg");
      background-position: 0 0;
      background-size: 15px 24px;
      background-repeat: no-repeat;
      position: absolute;
      top: 0;
      left: 0;
    }
  }
}

.contacts__tel {
  padding-left: 45px;
  margin-right: 30px;
  position: relative;
  font-size: 16px;
  line-height: 19px;
  font-weight: 500;
  color: @white;

  &::after {
    content: "";
    display: block;
    width: 24px;
    height: 24px;
    background-image: url("../img/001-phone_2-white.svg");
    background-position: 0 0;
    background-size: 24px 24px;
    background-repeat: no-repeat;
    position: absolute;
    top: 0;
    left: 0;
  }
}

.contacts__mail {
  padding-left: 45px;
  margin-right: 50px;
  position: relative;
  font-size: 16px;
  line-height: 19px;
  font-weight: 500;
  color: @white;

  &::after {
    content: "";
    display: block;
    width: 24px;
    height: 18px;
    background-image: url("../img/002-email_2-white.svg");
    background-position: 0 0;
    background-size: 24px 18px;
    background-repeat: no-repeat;
    position: absolute;
    top: 0;
    left: 0;
  }
}

.contacts__btn {
  margin-bottom: 75px;
}

.contacts__rights {
  font-size: 12px;
  line-height: 15px;
  color: @white;
}
